.tableActivities {
  width: 100%;
  text-align: center;
}
.tableActivities th {
  padding: 5px;
}
.tableActivities tr:nth-child(even),
.colBtnAddActivity  {
  background-color: #fff;
}
.tableActivities tr:nth-child(odd){
  background-color: #eee;
}
.colBtnAddActivity button {
  width: 99%;
}
.center-icon{
  display: flex;
  justify-content: center;
}